@font-face {
  font-family: 'head';
  src: url('../../assets/font/HYXiaoBoHuaBaoHeiW.ttf');
}
.content {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.content .icon {
  position: absolute;
  top: 0.35rem;
  left: 0.55rem;
  height: 1.1rem;
  width: 1.2rem;
  background: url('../../assets/image/flag.png') no-repeat center;
  background-size: 100%;
}
.content header {
  box-sizing: border-box;
  padding-top: 0.4rem;
  height: 4em;
  width: 8.5rem;
  background: url('../../assets/image/head.png') no-repeat center;
  background-size: 100%;
}
.content header div {
  text-align: center;
  height: 1rem;
}
.content header p {
  font-family: 'head';
  margin: 0 0.05rem;
  display: inline-block;
  font-size: 0.95rem;
  color: #fff;
  text-shadow: 4px 4px 0 #9a7352, -4px -4px 0 #9a7352, 4px -4px 0 #9a7352, -4px 4px 0 #9a7352;
}
.one {
  -webkit-transform: translateY(0.1rem) rotate(-10deg);
          transform: translateY(0.1rem) rotate(-10deg);
}
.two {
  -webkit-transform: translateY(-0.1rem) rotate(-5deg);
          transform: translateY(-0.1rem) rotate(-5deg);
}
.three {
  -webkit-transform: translateY(-0.15rem) rotate(-2.5deg);
          transform: translateY(-0.15rem) rotate(-2.5deg);
}
.four {
  -webkit-transform: translateY(-0.15rem) rotate(2.5deg);
          transform: translateY(-0.15rem) rotate(2.5deg);
}
.five {
  -webkit-transform: translateY(-0.1rem) rotate(5deg);
          transform: translateY(-0.1rem) rotate(5deg);
}
.six {
  -webkit-transform: translateY(0.1rem) rotate(10deg);
          transform: translateY(0.1rem) rotate(10deg);
}
